
import { defineComponent, ref } from "vue";
import useAuthUser from "../composables/useAuthUser";
export default defineComponent ({
  setup() {
      const { fetchToken } = useAuthUser();
      let email = ref("");
      let password = ref("");
      const getToken = () => {
          fetchToken({email: email.value, password: password.value});
      }
      return{
          email,
          password,
          getToken
      }
  }
});
